<template>
    <div class="box box-primary">
        <div class="box-header with-border">
            <h3 class="box-title">Đổi mật khẩu</h3>
        </div>
        <div class="box-body no-padding">
            <form v-on:submit.prevent="changePassword">
                <div class="box-body">
                    <div class="form-group">
                        <label>Mật khẩu cũ</label>
                        <input v-model="user.password" v-validate="'required'" name="password" type="password" class="form-control" placeholder="Nhập mật khẩu cũ...">
                        <span class="error">{{ errors.first("password") }}</span>
                    </div>
                    <div class="form-group">
                        <label >Mật khẩu mới</label>
                        <input v-model="user.passwordnew" v-validate="'required|min:6'" name="passwordnew" type="password" class="form-control" placeholder="Nhập mật khẩu mới..." id="passwordnew">
                        <span class="error">{{ errors.first("passwordnew") }}</span>
                    </div>
                    <div class="form-group">
                        <label>Nhập lại mật khẩu</label>
                        <input v-model="user.rpasswordnew" v-validate="'required|min:6|rPassword'" name="rpasswordnew" type="password" class="form-control" placeholder="Nhập lại mật khẩu mới..." id="rpasswordnew">
                        <span class="error">{{ errors.first("rpasswordnew") }}</span>
                    </div>
                </div>
                <div class="box-footer">
                    <button type="submit" class="btn btn-primary">Đổi</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import {mapGetters} from "vuex";
import {Validator} from "vee-validate";
import axios from "axios";

export default {
    metaInfo(){
		return {
			title: "Đổi mật khẩu",
			htmlAttrs: {
				lang: 'vi',
				amp: true
			}
		}
	},
    data(){
        return {
            api: this.$store.state.domainApi,
            file:null,
            image:null,
            user:{
                password:'',
                passwordnew:'',
                rpasswordnew:''
            },
            dictionary:{
                vi: {
                    custom: {
                        "password":{
                            required: "Vui lòng nhập mật khẩu !"
                        },
                        "passwordnew": {
                            required: "Vui lòng nhập mật khẩu mới !",
                            min: "Mật khẩu tối thiểu 6 ký tự !"
                        },
                        "rpasswordnew": {
                            required: "Vui lòng nhập lại mật khẩu mới !" ,
                            min: "Mật khẩu tối thiểu 6 ký tự !"
                        }
                    }
                }
            }
        }
    },
    methods:{
        onChangeImage(e){
            if(!e.target.files[0].type.match('image.*'))
            return this.$swal('Vui lòng chọn đúng định dạng hình ảnh!','','error');
            else{
                this.file = e.target.files[0];
                const render = new FileReader();
                render.onload = e => this.image = e.target.result;
                render.readAsDataURL(e.target.files[0]);
            }
        },
        changePassword(){
            this.$validator.validate().then(valid => {
                if (valid) {
                    const formData = new FormData();
                    //formData.set("username", this.$store.state.user.username);
                    formData.set("password", this.user.password);
                    formData.append("passwordnew", this.user.passwordnew);
                    
                    axios.put(`${this.api}/auth/changepassword`, formData,{
                        headers:{ Authorization: `Bearer ${this.$store.state.token}` }
                    })
                    .then(response=>{
                        if(response.data.success){
                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                            this.$router.push({name: "user.profile"});
                        }else this.$swal(response.data.message,'','error');
                    })
                }
            });
        }
    },
    computed:{
        ...mapGetters({aboutUser: 'getUser'})
    },
    created(){
        this.user.avata = this.aboutUser.avata;
        this.user.fullname = this.aboutUser.fullname;
        this.user.address = this.aboutUser.address;
        this.user.facebook = this.aboutUser.facebook;
        this.user.zalo = this.aboutUser.zalo;

        Validator.localize(this.dictionary);
        const validator = new Validator({ first_name: 'alpha' });
        validator.localize('vi');
        Validator.extend('rPassword', {
            getMessage: () => 'Mật khẩu không khớp !',
            validate(){
                let passwordnew = document.getElementById("passwordnew");
                let rpasswordnew = document.getElementById("rpasswordnew");
                if(passwordnew.value==rpasswordnew.value) return true;
                return false;
            }
        });
    }
}
</script>

<style>
    .error{
        color: red;
    }
    .form-group.avata_upload{
        text-align: center;
    } 
    .form-group.avata_upload label{
        position: relative;
    }
    .form-group.avata_upload label img{
        height: 100px;
        width: 100px;
        object-fit: cover;
    }
    .form-group.avata_upload label a{
        cursor: pointer;
        position: absolute;
        bottom: 15px;
        left: 17px;
        background: #0000008a;
        color: white;
        padding: 0px 4px;
        border-radius: 2px;
        font-weight: 500;
    }
    .form-group.avata_upload #avata_upload{
        display: none;
    } 
</style>